.settings-container {
  background: #f2f2f7;
  height: 100vh;
  overflow-y: auto;
  padding: 0 16px;
}

.settings-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 12px 0;
  position: relative;
}

.back-btn {
  position: absolute;
  left: 0;
  top: 12px;
  font-size: 18px;
  background: none;
  border: none;
  margin-left: 8px;
}

.app-title {
  font-size: 16px;
  font-weight: 500;
}

.page-title {
  font-size: 14px;
  color: #555;
  margin-top: 4px;
}

.settings-section {
  background: white;
  border-radius: 12px;
  margin-top: 16px;
  padding: 12px 16px;
}

.settings-section h3 {
  margin: 0 0 12px;
  font-size: 14px;
  color: #888;
}

.setting-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid #eee;
  cursor: pointer;
}

.setting-item:last-child {
  border-bottom: none;
}

.toggle-switch {
  position: relative;
  width: 40px;
  height: 24px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0; left: 0; right: 0; bottom: 0;
  background-color: #ccc;
  border-radius: 24px;
  transition: 0.2s;
}

.toggle-switch .slider::before {
  position: absolute;
  content: '';
  height: 18px; width: 18px;
  left: 3px; top: 3px;
  background-color: white;
  border-radius: 50%;
  transition: 0.2s;
}

.toggle-switch input:checked + .slider {
  background-color: #34c759;
}

.toggle-switch input:checked + .slider::before {
  transform: translateX(16px);
}

.slider-item {
  flex-direction: column;
  align-items: flex-start;
}

.slider-item input[type=range] {
  width: 100%;
  margin: 4px 0;
}

.slider-value {
  font-size: 12px;
  color: #666;
  margin-left: auto;
}
